<template>
	<div id="orders">
		<!--顶部菜单-->
		<nav-bar class="order-nav-bar">
			<div slot="center">
				{{title}}({{$store.state.OrderList[cindex]}}{{ordersLen}})
			</div>
			<div slot="right">
				总价:{{ordersAllPrice}}
			</div>
		</nav-bar>
		<!--菜单点击按钮-->
		<order-tab :text-arr="test" @orderTabClick="orderTabClick" class="order-tab"/>
		<!--滚动--->
		<scroll class="orders-scroller"
				ref="scroller"
				:probe-type=3
				@pullingup="loadMove"
		>
			<!--菜单显示内容-->
			<order-content :test="ordersArr" :current-type="currentType"/>
		</scroll>
	</div>
</template>

<script>
import NavBar from 'components/common/navbar/NavBar'
import OrderTab from 'components/contents/orderTab/OrderTab'
import OrderContent from './childComps/OrderContent'
import Scroll from 'components/common/scroll/Scroll'

export default {
  name: 'Orders',
  components: {
	NavBar,
	OrderTab,
	OrderContent,
	Scroll
  },
  data(){
	return{
		title:'订单',
		test:['今天订单','待发货','待付款','全部订单'],
		cindex:0,
		currentType:'tohdayOrder',			//		waitSendGoods,waitPayMoney,allOrders
	}
  },
  methods:{
	orderTabClick(index){
		console.log(index)
		this.cindex=index
		switch(index){
			case 0:
				this.currentType="tohdayOrder"
				break
			case 1:
				this.currentType="waitSendGoods"
				break
			case 2:
				this.currentType="waitPayMoney"
				break
			case 3:
				this.currentType="allOrders"
				break
		}

		/*let product={
			menpai:'化生???',level:69,serve:'一区-再续前缘',price:3687,oid:1,
		}
		this.$store.dispatch('addOrders',product).then(res=>{
			console.log(res)
		})*/
		
		this.$refs.scroller.finishPullUp()	//获取最新高度
	},
	loadMove(){
		//console.log("加载更多")
		this.$refs.scroller.refresh();		//刷新一下
		//this.$refs.scroller.finishPullUp()	//获取最新高度
	}
  },
  created(){
	//console.log(this.$store.state.OrdersList)
  },
  computed:{
	//订单数组
	ordersArr(){
		return this.$store.state.OrdersList[this.currentType].list
	},
	//有多少个订单
	ordersLen(){
		return this.$store.state.OrdersList[this.currentType].list.length
	},
	//计算订单一共多少钱
	ordersAllPrice(){
		let allprice=0;
		/*for(let i of this.$store.state.OrdersList[this.currentType].list){
			allprice+=i.price
		}*/
		this.$store.state.OrdersList[this.currentType].list.forEach((item)=>{allprice+=item.price})
		return allprice
	}
  }

}
</script >

<style>
#orders{
	background:#eaeaea;
    height: 100vh;
    position: relative;
}
.order-tab{
	background:#fff;
}
.order-nav-bar{
	background:#fff;
	border-bottom:1px solid #f1f1f1;
}
.orders-scroller{
    /*height:300px;*/
    position: absolute;
	height:calc(100% - 139px);
    right: 0;
    left: 0;
	overflow:hidden;
  }
</style>
