<template>
	<div class="order-content">
			<order-content-item v-for="(item,index) in test" :key="index" :obj="item">
				<div slot="bottominfo"  class="message-box">
					<span class="success">交易成功</span>
					<a class="delete" @click="deleteOrder(item.oid)">删除订单</a>
					<a class="update">修改订单</a>
					<a class="update" @click="detailClick(item.oid)">订单详情{{item.oid}}</a>
				</div>
			</order-content-item>
	</div>
</template>

<script>
import OrderContentItem from 'components/contents/orderItem/OrderContentItem'

export default {
  name: 'OrderContent',
  components:{
	OrderContentItem
  },
  data(){
	return{
		
	}
  },
  methods:{
	detailClick(oid){
		this.$router.push("/orderdetail/"+oid).catch(err=>{err})
	},
	deleteOrder(index){
		console.log("删除订单为 id:"+index)
		//删除数组里的一个东西
		let product={
			id:index,
			type:this.currentType,
		}
		//删除一个订单
		this.$store.dispatch('deleteOrder',product).then(res=>{
			console.log(res)
		})
	}
  },
  props:{
	test:{
		type:Array,
		default(){
			return []
		}
	},
	currentType:{
		type:String,
		default:'tohdayOrder'
	}
  }
}
</script>


